@import url("https://fonts.googleapis.com/css?family=Roboto:100,300,400,700|Noto+Sans+KR");
@import url("https://use.fontawesome.com/releases/v5.5.0/css/all.css");
.todos-body {
  font-family: "Roboto", "Noto Sans KR", sans-serif;
  font-size: 0.9em;
  color: #58666e;
  background-color: #f0f3f4;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;

  .container {
    max-width: 750px;
    min-width: 450px;
    margin: 0 auto;
    padding: 15px;
  }

  .title {
    /* margin: 10px 0; */
    font-size: 4.5em;
    font-weight: 100;
    text-align: center;
    color: #23b7e5;
  }

  .ver {
    font-weight: 100;
    text-align: center;
    color: #23b7e5;
    margin-bottom: 30px;
  }

  /* .input-todo  */
  .input-todo {
    display: block;
    width: 100%;
    height: 45px;
    padding: 10px 16px;
    font-size: 18px;
    line-height: 1.3333333;
    color: #555;
    border: 1px solid #ccc;
    border-color: #e7ecee;
    border-radius: 6px;
    outline: none;
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  }

  .input-todo:focus {
    border-color: #23b7e5;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
      0 0 8px rgba(102, 175, 233, 0.6);
  }

  .input-todo::-webkit-input-placeholder {
    color: #999;
  }

  /* .nav */
  .nav {
    display: flex;
    margin: 15px;
    list-style: none;
  }

  .nav > li {
    padding: 4px 10px;
    border-radius: 4px;
    cursor: pointer;
  }

  .nav > li.active {
    color: #fff;
    background-color: #23b7e5;
  }

  .todos {
    margin-top: 20px;
  }

  /* .todo-item */
  .todo-item {
    position: relative;
    /* display: block; */
    height: 50px;
    padding: 10px 15px;
    margin-bottom: -1px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-color: #e7ecee;
    list-style: none;
    animation: anim 0.5s linear forwards;
    animation-delay: calc(0.05s * var(--i));
  }

  .todo-item:first-child {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }

  .todo-item:last-child {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
  }

  /*
  .checkbox
  .checkbox    label before after 
  .checkbox     .

  <input class="checkbox" type="checkbox" id="myId">
  <label for="myId">Content</label>
*/

  .checkbox {
    display: none;
  }

  .checkbox + label {
    position: absolute; /*    */
    top: 50%;
    left: 15px;
    transform: translate3d(0, -50%, 0);
    display: inline-block;
    width: 90%;
    line-height: 2em;
    padding-left: 35px;
    cursor: pointer;
    user-select: none;
  }

  .checkbox + label:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate3d(0, -50%, 0);
    width: 20px;
    height: 20px;
    background-color: #fff;
    border: 1px solid #cfdadd;
  }

  .checkbox:checked + label:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 6px;
    transform: translate3d(0, -50%, 0);
    width: 10px;
    height: 10px;
    background-color: #23b7e5;
  }

  /* .remove-todo button */
  .remove-todo {
    display: none;
    position: absolute;
    top: 50%;
    right: 10px;
    cursor: pointer;
    transform: translate3d(0, -50%, 0);
  }

  /* todo-item      */
  .todo-item:hover > .remove-todo {
    display: block;
  }

  footer {
    display: flex;
    justify-content: space-between;
    margin: 20px 0;
  }

  .complete-all,
  .clear-completed {
    position: relative;
    flex-basis: 50%;
  }

  .clear-completed {
    text-align: right;
    padding-right: 15px;
  }

  .btn {
    padding: 1px 5px;
    font-size: 0.8em;
    line-height: 1.5;
    border-radius: 3px;
    outline: none;
    color: #333;
    background-color: #fff;
    border-color: #ccc;
    cursor: pointer;
  }

  .btn:hover {
    color: #333;
    background-color: #e6e6e6;
    border-color: #adadad;
  }
  @keyframes anim {
    0% {
      opacity: 0;
      font-size: 0%;
    }
    50% {
      opacity: 1;
      font-size: 200%;
      color: rgb(73, 172, 202);
    }
    100% {
      font-size: 100%;
    }
  }
  .todos:hover time {
    opacity: 1;
  }
  .todo-item:hover time {
    color: #1188aa;
    font-size: 1.2em;
    background: rgba(255, 255, 255, 0.8);
  }
  time {
    transition: 0.4s linear;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    line-height: 50px;
    text-align: center;
    pointer-events: none;
  }
  i {
    z-index: 10000;
  }
  .todo-item.bye {
    animation: bye 1.1s ease-in-out;
  }
  @keyframes bye {
    0% {
      clip-path: circle(100%);
    }
    100% {
      clip-path: circle(00%);
    }
  }
}
